<template>
  <div>
    <h2></h2>
    <h4></h4>
    <h4></h4>
    <h4></h4>
    <div style="padding-left:20px">
      function (a,b,c,d){
      <br />console.log(a,b,c,d)
      <br />}
      <br />foo(1,2) -> 1,2,undefined,undefined
      <br />
      <p></p>let foo2 = currying(foo) <br>
       foo2(1)(2)(3)(4)  -> 1,2,3,4
    </div>
    <div></div>
    <div></div>
    <div class="mdBox">
        <pre>
        function foo (a,b,c,d){
            console.log(a,b,c,d)
        } // 

        //   
        function currying(fn, ...args){
            // fn fn.length
            if(args.length >= fn.length){
                return fn(...args)
            }else{
                // 
                return (args) => currying(fn, ...args, ...args2)
            }
        }
        const foo2 = currying(foo)
        // 

         foo2(1)(2)(3)(4)  -> 1,2,3,4

         // 
        </pre>
    </div>
  </div>
</template>

<script>
export default {
  name: "FunCurrying",
  data() {
    return {};
  },
  methods: {
    foo(a, b, c, d) {
      console.log(a, b, c, d);
    },

    currying(fn,...args) {
        if (args.length >= fn.length) {
          return fn(...args);
        } else {
          return (...args2) => this.currying(fn, ...args, ...args2);
        }
    }
  },
  mounted() {
      const foo2 = this.currying(this.foo)
      foo2(1)(2)(3)(4)
  }
};
</script>

